
.swipe-fullscreen,
.swipe-fullscreen > .swipe-wrap,
.swipe-fullscreen > .swipe-wrap > .swipe-item {
  width: 100%;
  height: 100%;
}
.swipe-fullscreen .swipe-wrap > .swipe-item {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: bold;
  color: #fff;
  font-size: 20px;
  text-align: center;
}



